.role {
  font-size: 3rem;
  letter-spacing: 0.2rem;
}
.role-a {
  letter-spacing: 0.9rem;
  margin-top: 45px;
}
.role-container {
  right: 8vw;
  top: 30%;
  position: absolute;
}
.profile {
  width: 95%;
  height: 95%;
  position: absolute;
  object-fit: contain;
  left: -15%;
  bottom: -5%;
}
.project-btn-container {
  margin-top: 150px;
  margin-left: 100px;
  display: flex;
  position: relative;
}
.arrow-anim {
  height: 15vh;
  width: 18vw;
}

@media only screen and (max-width: 992px) {
  .role {
    font-size: 2.3rem;
  }
  .role-a {
    font-size: 1.2rem;
    letter-spacing: 0.7rem;
  }
}

@media only screen and (max-width: 768px) {
  .profile {
    position: relative;
    width: 100%;
    height: 45%;
    left: 0;
    bottom: 0;
  }
  .role {
    font-size: 1.8rem;
    letter-spacing: 0.1rem;
    text-align: center;
  }
  .role-a {
    font-size: 0.9rem;
    letter-spacing: 0.5rem;
    text-align: center;
    margin-top: 30px;
  }
  .role-container {
    top: 0;
    right: 0;
    position: relative;
  }
  .project-btn-container {
    margin-top: 40px;
    margin-left: 10%;
    display: flex;
    position: relative;
  }
  .arrow-anim {
    height: 15vh;
    width: 35vw;
  }
}
